.about-description_wrap {
  display: flex;
  padding-bottom: 80px;
  margin-bottom: 80px;
  border-bottom: 2px solid #E9E9E9;
}

.about-description_wrap .left {
  width: 50%;
  margin-right: 130px;
}

.about-description_wrap .left h4 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 15px;
}

.about-description_wrap .left p {
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 15px;
}

.about-description_wrap .left p:last-child {
  margin-bottom: 0;
}

.about-description_wrap .left .description-wrap {
  margin-bottom: 25px;
}

.about-description_wrap .left .services-button {
  display: block;
  width: 105px;
  background: #23A8CD;
  text-align: center;
  color: #fff;
  padding: 15px 0;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  border-radius: 12px;
  box-shadow: 0 8px 24px 0 rgba(24, 120, 147, 0.24);
}

.about-description_wrap .left .services-button:hover {
  color: #fff !important;
  background-color: #1c84a1;
}

.about-description_wrap .right {
  width: 41%;
  overflow: hidden;
}

.about-description_wrap .right:hover img {
  transform: scale(1.1);
}

.about-description_wrap .right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

@media screen and (max-width: 990px) {
  .about-description_wrap {
    flex-direction: column-reverse;
  }
  .about-description_wrap .left {
    width: 100%;
    margin-right: 0;
  }
  .about-description_wrap .right {
    width: 100%;
    margin-bottom: 40px;
    height: 600px;
  }
  .about-description_wrap .right img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

@media screen and (max-width: 990px) {
  .about-description_wrap {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .about-description_wrap .description-wrap {
    margin-bottom: 15px;
  }
  .about-description_wrap .left h4 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 4px;
  }
  .about-description_wrap .left p {
    font-size: 14px;
    line-height: 23px;
  }
  .about-description_wrap .left .services-button {
    width: 100px;
    padding: 12px 0;
  }
  .about-description_wrap .right {
    height: 290px;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 25px;
  }
}

